@import "components/variables.less";

.btn-group-radio {
    margin-bottom: 40px;
}

.form-cand,
.form-empl,
.form-empl-st1,
.form-empl-st2 {
    h2 {
        margin-top: 0;
    }
    
    .form-block {
        margin-bottom: 40px;
    }

    .form-group {
        > label {
            white-space: nowrap;
        }
    }
    
    .form-control-sm {
        width: 140px;
    }

    .radio-inline {
        padding-left: 27px;

        input[type="radio"] {
           margin-left: -27px;
        }
    }
    
    .checkbox label {
        padding-left: 27px;

        input[type="checkbox"] {
           margin-left: -27px;
        }        
    }
    
    .block-radio-inline {
        padding-top: 7px;
        
        .radio-inline {
            margin-right: 30px;
            
            &:last-child {
                margin-right: 0;
            }
        }
    }
    
    .btn-remove,
    .btn-download {
        background-color: #fff;
        font-size: 16px;
        line-height: 16px;

        &:active {
          -webkit-box-shadow: none;
                  box-shadow: none;
        }
    }

    .btn-remove {
        color: #ff0000;

        &:hover,
        &:focus,
        &:active {
            color: darken(#ff0000, 15%);
        }        
    }    
    
    .table-docs {
        width: auto;
        border-top: 0;
        border-bottom: 0;
        font-size: inherit;
        
        > tbody > tr > td {
            border: 0;
            padding-bottom: 0;
            padding-left: 4px;
            padding-right: 4px;
        }

        > tbody > tr:first-child > td {
            padding-top: 0;
        }

        .cell-name {
            padding-left: 0;
            padding-right: 16px;
            max-width: 400px;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
            vertical-align: middle;

            > a {
                color: #000;
                text-decoration: none;
                font-weight: 600;

                &:hover,
                &:focus,
                &:active {
                    color: #000;
                    text-decoration: none;
                }
            }
        }

        .btn-remove {
            margin-top: -1px;
        }
    }  
    
    .form-group-btns {
        margin-top: 25px;
        white-space: nowrap;
        
        .btn {
            margin-right: 6px;
            
            &:last-child {
                margin-right: 0;
            }
        }
    }  
}

.form-cand {
    .form-block {
        margin-bottom: 20px;
    } 
}

.form-empl {
    .form-block-type-list {
        .form-group-btns {
            margin-top: -50px;
        }
    }
    
    .form-group-list {
        clear: both;
    }
    
    .form-btns {
        .btn {
            margin-left: 10px;
        }
    }
}

//************ Kladr
#kladr_autocomplete {
    a {
        color: #333;
    }
}

@import "emplform-st1.less";

@import "emplform-st2.less";

@import "candform-st1.less";

@import "candform-st2.less";

@media (min-width: 768px) {
    .form-empl-st1,
    .form-empl-st2 {
        .form-control-inp-text {
            white-space: nowrap;
            height: 34px;
            resize: none;
            overflow: hidden;
        }
    }
}

@media (max-width: 767px) {
    .btn-group-radio {
        margin-bottom: 30px;
    }
    
    .form-empl-st1,
    .form-empl-st2,
    .form-empl {
        margin-bottom: 45px;

        .form-block {
            margin-bottom: 20px;
        }

        .form-group {
            > label {
                white-space: normal;
            }
        }

        .form-control-inp-text {
            height: 80px;
        }
        
        .table-docs {
            .cell-name {
                max-width: 200px;
            }
        }
        
        .form-btns { 
            margin-top: 10px;
        }
        
        .form-group-btns {
            margin-top: 0;
        }
    }
    
    .form-empl {
        .form-block-type-list {
            .form-group-btns {
                margin-top: -0px;
            }
            
            .help-block {
                margin-bottom: 0;
            }
        }

        .form-btns {
            .btn {
                margin: 0 20px 0 0;
                width: -webkit-calc(~'50% - 10px');
                width: -moz-calc(~'50% - 10px');
                width: calc(~'50% - 10px');
                padding-left: 0;
                padding-right: 0;
                
                &:first-child {
                    margin: 0;
                }
            }
        }        
    }
}